




























import { getUrlParam } from '@/utils/common.ts'
import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {},
})
export default class ViewComponent extends Vue {
  /**data */
  selectedLabel = '' // 提现页，成功页，失败页
  model = {
    amount: '',
  }
  schema = {
    fields: [
      {
        type: "input",
        modelKey: "amount",
        label: "￥",
        props: {
          placeholder: "输入金额",
          clearable: true,
        },
        rules: {
          required: true,
        },
        trigger: "blur",// *待优化：这样会触发多次异步请求校验。思路一：仅当提交表单时才发起一次异步校验
        messages: {
          required: "不能为空",
        },
      },
      {
        type: "submit",
        label: "确认"
      }
    ]
  }
  availableWithdrawAmt = 0;

  /**life hook */
  created(){
    if(getUrlParam('result') === 'true'){
      this.selectedLabel = '成功页'
    }
    else if(getUrlParam('result') === 'false'){
      this.selectedLabel = '失败页'
    }
    else{
      this.selectedLabel = '提现页'
      this.initInfoShow()
    }
  }

  /**method */
  async submitHandler(e: any) {
    e.preventDefault();
    if(+this.model.amount > this.availableWithdrawAmt){
      this.$createToast({
        type: 'warn',
        time: 2000,
        txt: '超过可提现金额'
      }).show();
      return;
    }

    const data = await this.$request({
      api: 'lmWithdrawConfirm',
      params: {
        amount: this.model.amount,
        token:this.$store.state.token,
        type: 0,
        originReturnUrl: this.$store.state.originReturnUrl,
      },
      method:'post',
    })
    if(data.resCode === '0000'){
      this.$request({
        formSubmitData:{
          url:data.shUrl,
          params:data.params,
          returnUrl:data.returnUrl,
        }
      })
    }
  }
  async initInfoShow(){
    const data = await this.$request({
      api: 'lmWithdraw',
      params: {
        token:this.$store.state.token,
      },
      method:'post',
    })
    if(data.resCode === '0000'){
      this.availableWithdrawAmt = data.availableWithdrawAmt
    }
  }
  
}
